import changeHeader from'../../components/changeHeader.vue'
import changeList from'../../components/changeList.vue'
import changeAlphabet from'../../components/changeAlphabet.vue'
export default {
	components:{
		changeHeader,
		changeList,
		changeAlphabet
	},
	data() {
		return {
			city:[],
			letter: ''
		}
	},
	methods: {
		getCityInfo(){
			uni.request({
				url:'http://localhost:8088/json/city.json',
				success: (res) => {
					this.city=res.data.city
				}
			})
		},
		change(res) {
			this.letter = res;
		}
	},
	created() {
		this.getCityInfo()
	}
}
